header {
  width: 100%;
  height: 150px;

  padding-top: 50px;

  text-align: center;

  background-color: #837979;
}

main {
  text-align: center;
  padding-top: 60px;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

span {
  margin: 0;
  padding: 0;
}

.container {
  width: 882px;

  margin: 0 auto;
}

.gallery-trending {
  &__list {
    display: flex;
    flex-wrap: wrap;

    margin-top: -30px;
    margin-left: -30px;
  }

  // &__item {
  //   margin-top: 30px;
  //   margin-left: 30px;
  // }
}

.card-thumb {
  margin-top: 30px;
  margin-left: 30px;

  width: 274px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__img {
    width: 274px;
    height: 398px;

    background-color: gray;
  }

  &__title,
  &__info {
    display: inline-block;
    margin-top: 10px;

    font-family: Roboto;
    font-style: normal;
    font-size: 12px;
    line-height: 1.33;
  }

  &__title {
    text-transform: uppercase;
    font-weight: 700;
  }

  &__info {
    font-weight: 500;
    color: #ff6b08;
  }
}
